define('settingsReader',['jsonReader'], function (jsonReader) {

    function readTemplateSettings() {
        return jsonReader.read('settings.js?v=1438173181627');
    }

    function readPublishSettings() {
        return jsonReader.read('publishSettings.js?v=1438173181627');
    }

    return {
        readTemplateSettings: readTemplateSettings,
        readPublishSettings: readPublishSettings
    };

});
